import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import api from "../../apis/api"; 

export const add_product = createAsyncThunk(
    "product/add_product",
    async (product, { rejectWithValue, fulfillWithValue, getState }) => {
      try {
        const token = localStorage.getItem("userToken");
        const sellerId = getState().auth.userInfo?.user_id || localStorage.getItem("sellerID");
  
        if (!token || !sellerId) {
          return rejectWithValue({
            message: "Thiu thng tin xc thc (token hoc sellerId).",
            statusCode: 401
          });
        }
  
 
        const formattedPayload = { 
          productData: {
            name: product.get('name'),
            descriptions: product.get('descriptions'),
            price: Number(product.get('price')),
            quantity: Number(product.get('quantity')),
            is_available: product.get('is_available') === 'true',
            is_draft: product.get('is_draft') === 'true'
          }
        };
        
        // X l ring cho image nu c
        if (product.get('image') instanceof File) {

        }
  
        const { data } = await api.post(
          `/products`,
          formattedPayload,
          {
            headers: {
              Authorization: token,
              "x-client-id": sellerId,
              "Content-Type": "application/json"
            },
          }
        );
  
        return fulfillWithValue(data.metadata);
      } catch (error) {
        console.error("API Error:", error);
        
        // X l li 404
        if (error.response && error.response.status === 404) {
          return rejectWithValue({
            message: "Khng tm thy ti nguyn yu cu (404). Vui lng kim tra li endpoint API.",
            statusCode: 404
          });
        }
        
        // X l li 500
        if (error.response && error.response.status === 500) {
          return rejectWithValue({
            message: "Li my ch (500). Vui lng th li sau hoc lin h qun tr vin.",
            statusCode: 500
          });
        }
        
        // X l cc li khc
        const errorMessage = error.response?.data?.message || 
                            error.message || 
                            " xy ra li khng xc nh. Vui lng th li.";
        
        return rejectWithValue({
          message: errorMessage,
          statusCode: error.response?.status || 0
        });
      }
    }
);





const productSlice = createSlice({
    name: 'product',
    initialState: {
        successMessage: '',
        errorMessage: '',
        loader: false,
        products: [],
        product: '',
        totalProduct: 0,
        statusCode: null
    },
    reducers: {
        messageClear: (state) => {
            state.successMessage = '';
            state.errorMessage = '';
            state.statusCode = null;
        }
    },
    extraReducers: (builder) => {
        builder
            .addCase(add_product.pending, (state) => {
                state.loader = true;
                state.errorMessage = '';
                state.successMessage = '';
                state.statusCode = null;
            })
            .addCase(add_product.rejected, (state, action) => {
                state.loader = false;
                
                if (typeof action.payload === 'object') {
                    state.errorMessage = action.payload.message || ' xy ra li. Vui lng th li.';
                    state.statusCode = action.payload.statusCode || null;
                } else {
                    state.errorMessage = action.payload || ' xy ra li. Vui lng th li.';
                }
            })
            .addCase(add_product.fulfilled, (state, action) => {
                state.loader = false;
                state.successMessage = action.payload.message || 'Thm sn phm thnh cng';
                state.statusCode = 200;
            });
    }
});

export const { messageClear } = productSlice.actions;
export default productSlice.reducer;
