import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import api from "../../apis/api";

// Create coupon
export const create_coupon = createAsyncThunk(
  "admin/create_coupon",
  async ({ body }, { rejectWithValue, fulfillWithValue, getState }) => {
    try {
      const token = localStorage.getItem("userToken");
      const adminId =
        getState().auth.userInfo?.user_id || localStorage.getItem("adminId");

      if (!token || !adminId) {
        throw new Error("Thiu thng tin xc thc (token hoc adminId).");
      }

      const { data } = await api.post(
        `/coupon`,
        body,
        {
          headers: {
            Authorization: token,
            "x-client-id": adminId,
          },
        }
      );

      // Gi s API tr v: { message, coupon }
      return fulfillWithValue(data.metadata);
    } catch (error) {
      return rejectWithValue(
        error.response?.data?.message ||
          "Li t server (500). Kim tra li API."
      );
    }
  }
);

// Get coupons
export const get_coupons = createAsyncThunk(
  "admin/get_coupons",
  async (_, { rejectWithValue, fulfillWithValue, getState }) => {
    try {
      const token = localStorage.getItem("userToken");
      const adminId =
        getState().auth.userInfo?.user_id || localStorage.getItem("adminId");

      if (!token || !adminId) {
        throw new Error("Thiu thng tin xc thc (token hoc adminId).");
      }

      const { data } = await api.get(`/admin/coupon`, {
        headers: {
          Authorization: token,
          "x-client-id": adminId,
        },
      });
      // Gi s API tr v: { metadata: [coupon, ...] }
      return fulfillWithValue(data.metadata);
    } catch (error) {
      return rejectWithValue(
        error.response?.data?.message ||
          "Li t server (500). Kim tra li API."
      );
    }
  }
);

// Edit coupon
export const edit_coupon = createAsyncThunk(
  "admin/edit_coupon",
  async ({ body }, { rejectWithValue, fulfillWithValue, getState }) => {
    try {
      const token = localStorage.getItem("userToken");
      const adminId =
        getState().auth.userInfo?.user_id || localStorage.getItem("adminId");

      if (!token || !adminId) {
        throw new Error("Thiu thng tin xc thc (token hoc adminId).");
      }

      const { data } = await api.put(
        `/admin/coupon`,
        {
          coupon_id: body.id,
          ...body,
        },
        {
          headers: {
            Authorization: token,
            "x-client-id": adminId,
          },
        }
      );

      // Gi s API tr v: { message, coupon }
      return fulfillWithValue(data.metadata);
    } catch (error) {
      return rejectWithValue(
        error.response?.data?.message ||
          "Li t server (500). Kim tra li API."
      );
    }
  }
);

const couponSlice = createSlice({
  name: "coupon",
  initialState: {
    successMessage: "",
    errorMessage: "",
    loader: false,
    coupons: [],
    coupon: null,
    totalCoupons: 0,
    statusCode: null,
  },
  reducers: {
    messageClear: (state) => {
      state.successMessage = "";
      state.errorMessage = "";
      state.statusCode = null;
    },
  },
  extraReducers: (builder) => {
    builder
      // CREATE COUPON
      .addCase(create_coupon.pending, (state) => {
        state.loader = true;
        state.errorMessage = "";
        state.successMessage = "";
        state.statusCode = null;
      })
      .addCase(create_coupon.rejected, (state, action) => {
        state.loader = false;
        state.errorMessage =
          action.payload || " xy ra li. Vui lng th li.";
        state.statusCode = null;
      })
      .addCase(create_coupon.fulfilled, (state, action) => {
        state.loader = false;
        state.successMessage =
          action.payload.message || "Thm m gim gi thnh cng";
        state.statusCode = 200;
        // Thm coupon mi vo u danh sch nu API tr v coupon
        if (action.payload?.coupon) {
          state.coupons = [action.payload.coupon, ...state.coupons];
          state.totalCoupons = state.coupons.length;
        }
      })

      // GET COUPONS
      .addCase(get_coupons.pending, (state) => {
        state.loader = true;
        state.errorMessage = "";
      })
      .addCase(get_coupons.rejected, (state, action) => {
        state.loader = false;
        state.errorMessage =
          action.payload || "Li khi ti danh sch m gim gi";
      })
      .addCase(get_coupons.fulfilled, (state, action) => {
        state.loader = false;
        state.coupons = action.payload || [];
        state.totalCoupons = state.coupons.length;
      })

      // EDIT COUPON
      .addCase(edit_coupon.pending, (state) => {
        state.loader = true;
        state.errorMessage = "";
        state.successMessage = "";
        state.statusCode = null;
      })
      .addCase(edit_coupon.rejected, (state, action) => {
        state.loader = false;
        state.errorMessage =
          action.payload || " xy ra li khi cp nht. Vui lng th li.";
        state.statusCode = null;
      })
      .addCase(edit_coupon.fulfilled, (state, action) => {
        state.loader = false;
        state.successMessage =
          action.payload.message || "Cp nht m gim gi thnh cng";
        state.statusCode = 200;
        // Update coupon trong danh sch nu API tr v coupon
        if (action.payload?.coupon) {
          state.coupons = state.coupons.map((coupon) =>
            coupon.id === action.payload.coupon.id
              ? action.payload.coupon
              : coupon
          );
        }
      });
  },
});

export const { messageClear } = couponSlice.actions;
export default couponSlice.reducer;
