import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import api from "../../apis/api";

// Thunk action  ly tt c n hng
export const get_allOrder = createAsyncThunk(
  "admin/get_allOrder",
  async (_, { rejectWithValue, fulfillWithValue, getState }) => {
    try {
      const token = localStorage.getItem("userToken");
      const adminId =
        getState().auth.userInfo?.user_id || localStorage.getItem("adminId");

      if (!token || !adminId) {
        return rejectWithValue("Khng c quyn truy cp, vui lng ng nhp.");
      }

      const { data } = await api.get("/admin/order", {
        headers: {
          Authorization: token,
          "x-client-id": adminId,
        },
      });

      // Tr v metadata (l mt mng)
      return fulfillWithValue(data.metadata);
    } catch (error) {
      console.error(" Li API:", error?.response?.data || error.message);
      return rejectWithValue(
        error.response?.data?.message || "Khng th kt ni n server."
      );
    }
  }
);
export const get_order_detail = createAsyncThunk(
  "order/get_detail",
  async (orderId, { rejectWithValue, fulfillWithValue, getState }) => {
    try {
      const token = localStorage.getItem("userToken");
      const adminId =
        getState().auth.userInfo?.user_id || localStorage.getItem("adminId");

      if (!token || !adminId) {
        return rejectWithValue("Khng c quyn truy cp, vui lng ng nhp.");
      }

      const { data } = await api.get(`/admin/order/${orderId}`, {
        headers: {
          Authorization: token,
          "x-client-id": adminId,
        },
      });

      return fulfillWithValue(data.metadata);
    } catch (error) {
      console.error(" Li API:", error?.response?.data || error.message);
      return rejectWithValue(
        error.response?.data?.message || "Khng th ly chi tit n hng."
      );
    }
  }
);
const orderReducer = createSlice({
  name: "order",
  initialState: {
    successMessage: "",
    errorMessage: "",
    loader: false,
    orders: [], // D liu n hng
    totalOrders: 0, // Tng s n hng
    orderDetail: null, // Chi tit n hng
  },
  reducers: {
    messageClear: (state) => {
      state.successMessage = "";
      state.errorMessage = "";
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(get_allOrder.pending, (state) => {
        state.loader = true;
        state.errorMessage = "";
      })
      .addCase(get_allOrder.fulfilled, (state, { payload }) => {
        state.loader = false;
        state.orders = payload || []; // Gn mng metadata vo orders
        state.totalOrders = payload.length || 0; // Tnh tng s n hng da trn  di ca mng
      })
      .addCase(get_allOrder.rejected, (state, { payload }) => {
        state.loader = false;
        state.errorMessage = payload || "Khng th ly danh sch n hng.";
      })

      .addCase(get_order_detail.pending, (state) => {
        state.loader = true;
        state.errorMessage = "";
      })
      .addCase(get_order_detail.fulfilled, (state, { payload }) => {
        state.loader = false;
        state.orderDetail = payload;
      })
      .addCase(get_order_detail.rejected, (state, { payload }) => {
        state.loader = false;
        state.errorMessage = payload;
        state.orderDetail = null;
      });
  },
});

export const { messageClear } = orderReducer.actions;
export default orderReducer.reducer;
